:checked + .ui-checkbox-label:before, :disabled + .ui-checkbox-label:before, :checked + .ui-checkbox-label:after, :disabled + .ui-checkbox-label:after {
  content: '';
  position: absolute;
  height: 2px;
  background-color: #fff; }

.ui-radio-label:hover {
  border-color: #aaa; }

:checked + .ui-checkbox-label, :disabled + .ui-checkbox-label {
  position: relative; }
  :checked + .ui-checkbox-label:before, :disabled + .ui-checkbox-label:before {
    width: 6px;
    top: 50%;
    left: calc(50% - 6px);
    -webkit-transform: rotate(45deg);
    transform: rotate(45deg); }
  :checked + .ui-checkbox-label:after, :disabled + .ui-checkbox-label:after {
    width: 10px;
    top: calc(50% - 1px);
    left: calc(50% - 3px);
    -webkit-transform: rotate(-45deg);
    transform: rotate(-45deg); }

.ui-checkbox {
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.ui-checkbox-label {
  border: 2px solid transparent;
  box-shadow: inset 0 1px, inset 1px 0, inset -1px 0, inset 0 -1px;
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 4px;
  box-sizing: border-box;
  background-color: #fff;
  color: #d0d0d5;
  vertical-align: -.4em;
  background-clip: content-box;
  transition: color .2s, background-color .1s;
  overflow: hidden; }
  .ui-checkbox-label:hover {
    color: #aaa; }

.ui-radio {
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.ui-radio-label {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #d0d0d5;
  border-radius: 100%;
  background-color: #fff;
  box-sizing: border-box;
  transition: border-color .2s;
  overflow: hidden;
  vertical-align: -.3em; }
  .ui-radio-label:before {
    content: '';
    display: block;
    width: 10px;
    height: 10px;
    border-radius: 10px;
    background-color: #00a5e0;
    margin: 4px auto;
    visibility: hidden; }

.ui-switch {
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.ui-switch-label {
  display: inline-block;
  width: 44px;
  height: 26px;
  border: 2px solid;
  color: #d0d0d5;
  border-radius: 26px;
  background-color: currentColor;
  box-sizing: border-box;
  cursor: pointer;
  vertical-align: -.3em;
  transition: all .2s; }
  .ui-switch-label:before {
    content: '';
    display: block;
    width: 22px;
    height: 22px;
    border-radius: 22px;
    background-color: #fff;
    transition: margin-left .2s; }

.ui-file {
  position: absolute;
  clip: rect(0, 0, 0, 0); }

.ui-file-label {
  display: inline-block;
  height: 20px;
  padding: 10px 15px;
  color: #fff;
  background-color: #00a5e0;
  border-radius: 4px; }

:checked + .ui-checkbox-label {
  color: #00a5e0;
  background-color: #00a5e0; }

:checked + .ui-radio-label:before {
  visibility: visible; }

:checked + .ui-switch-label {
  color: #00a5e0; }
  :checked + .ui-switch-label:before {
    margin-left: 18px; }

:disabled + .ui-checkbox-label {
  color: #a5a5a5;
  background-color: #a5a5a5; }

:disabled + .ui-radio-label:before {
  visibility: visible;
  background-color: #a5a5a5; }

